import React from "react"
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";
import headerBackground from 'assets/images/banner4.jpg';

export default class Panorama360Viewer extends React.Component {
    constructor() {
        super();
        this.state = { playButtonVisibility: 'visible' };
    }

    render() {
        return (
            <div className='homepage center center-page-x'>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>Add interactive 3d and AR to your website</title>
                    <link rel="canonical" href="https://www.ezyvue.com/platform/360panoramaviewer" />
                </Helmet>
                <section className="full-width-header"
                    style={{
                        backgroundImage: `url(${headerBackground})`,
                        justifyContent: 'left',
                        backgroundPosition: '80%'
                    }
                    }>
                    <div style={{
                        maxWidth: '800px',
                        marginLeft: '5  %'
                    }}>
                        <h1>
                            EzyVue Panorama 3D and VR Viewer
                        </h1>
                        <h3>

                        </h3>
                        {/*        <span style={{ color: '#FF9913' }}>Plans from $0 per month</span> */}
                    </div>
                    <div className='full-width-header-promo-button'>
                        <div className="homepage-button-medium">
                            <Link to='/pricing'>Get started for free</Link>
                        </div>
                    </div>
                </section>
                <div className='homepage-section'>
                    <div style={{ maxWidth: '800px' }}>
                        <h1> Share your 360 photographs on your website in 3D and VR </h1>
                        <h4 style={{ margin: 'auto' }}>
                            Upload, manage and edit your panoramas using a single web platform.<br/>
                            Simply insert the given iframe tag on a webpage, no software installation is required.
                        </h4>
                        <br />
                        <div>Click to view demo</div>
                        <div style={{ width: '100%', maxWidth: '800px', margin: 'auto' }}>
                                <Link to='/panoramaViewer?panoramaCollectionId=640726a7b75a4698df288892'>
                                    <img src={require('../../assets/images/demo360.jpg')} alt='view panorama demo' style={{width: '100%'}} />
                                </Link>
                        </div>
                    </div>
                    <br />
                </div >
            </div >
        );
    }
}